import { getComments, addComment } from "@/apis/article";

export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [], // 文章列表
      text: "",
    };
  },
  async mounted() {
    const { data } = await getComments(this.article.slug);
    this.comments = data.comments;
  },
  methods: {
    addComment() {
      addComment(this.article.slug, {
        comment: {
          body: this.text,
        },
      });
    },
  },
};
